import React, { useEffect, useState } from "react";
import Navigation from "../../components/Navigation";
import Topbar from "../../components/Topbar";
import { Link } from "react-router-dom";
import { axiosInstance, renderUrl } from "../../config";
import Swal from "sweetalert2";
import moment from "moment";
const Allfpo = () => {
  const token = localStorage.getItem("admin");
  const [fpos, setfpos] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchAdminList = async () => {
      try {
        // Update the URL to include a query parameter to filter by role
        const response = await axiosInstance.get("fpo/allfpo", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setfpos(response.data.fpos);
      } catch (err) {
        setError("Error while fetching admin list");
      }
    };

    fetchAdminList();
  }, []);

  const [editFpo, setEditFpo] = useState(null);
  const [showfpodetails, setshowfpodetails] = useState(false);
  const [selectedfpo, setselectedfpo] = useState(null);

  const [editFormData, setEditFormData] = useState({
    name: "",
    registeredOn: "",
    promoter: "",
    sharePerMember: "",
    numberOfDirectors: "",
  });

  const openEditModal = (fpo) => {
    setEditFpo(fpo);
    setEditFormData({
      name: fpo.name,
      registeredOn: moment(fpo.registeredOn).format("YYYY-MM-DD"),
      promoter: fpo.promoter,
      sharePerMember: fpo.sharePerMember,
      numberOfDirectors: fpo.numberOfDirectors,
    });
  };

  // Function to handle form input changes
  const handleEditFormChange = (e) => {
    const { name, value } = e.target;
    setEditFormData({
      ...editFormData,
      [name]: value,
    });
  };

  // Function to submit the edited FPO details
  const submitEditForm = async () => {
    // Show a confirmation dialog
    const result = await Swal.fire({
      title: "Confirmation",
      text: "Are you sure you want to update these FPO details?",
      icon: "question",
      showCancelButton: true,
      confirmButtonText: "Yes",
      cancelButtonText: "No",
    });
    if (result.isConfirmed) {
      // User confirmed, proceed with the form submission
      try {
        // Make a request to update FPO details
        const response = await axiosInstance.put(
          `fpo/edit/${editFpo._id}`,
          editFormData,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        // Update the FPO list with the edited FPO
        const updatedFpos = fpos.map((fpo) =>
          fpo._id === editFpo._id ? response.data.fpo : fpo
        );

        setfpos(updatedFpos);
        setEditFpo(null);

        // Show a success message
        Swal.fire({
          icon: "success",
          title: "Success",
          text: "FPO details updated successfully.",
        });
      } catch (err) {
        console.error(err);

        // Show an error message
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "An error occurred while editing FPO details.",
        });
      }
    }
  };

  return (
    <div>
      {/* <!-- Layout wrapper --> */}
      <div className="layout-wrapper layout-content-navbar">
        <div className="layout-container">
          {/* <!-- Menu --> */}

          <Navigation />
          {/* <!-- / Menu --> */}

          {/* <!-- Layout container --> */}
          <div className="layout-page">
            <Topbar />
            {/* <!-- / Layout page --> */}
            <div className="content-wrapper">
              {/* Content */}
              {!showfpodetails && (
                <div className="container-xxl flex-grow-1 container-p-y">
                  <h4 className="py-3 mb-4">
                    <span className="text-muted fw-light">FPO /</span> FPO's
                    list
                  </h4>
                  {/* Basic Bootstrap Table */}
                  <div className="card">
                    <h5 className="card-header">All FPO</h5>
                    <div className="table-responsive text-nowrap">
                      <table className="table">
                        <thead>
                          <tr>
                            <th>
                              <b>FPO</b>
                            </th>
                            <th>
                              <b>Members</b>
                            </th>
                            <th>
                              <b>Promoter</b>
                            </th>
                            <th>
                              <b>Share per Member</b>
                            </th>
                            <th>
                              <b>No. of Directors</b>
                            </th>
                            <th>
                              <b>Admin</b>
                            </th>
                            <th>
                              <b>Registered On</b>
                            </th>
                            <th>
                              <b>Status</b>
                            </th>
                            <th>
                              <b>Actions</b>
                            </th>
                          </tr>
                        </thead>
                        <tbody className="table-border-bottom-0">
                          {fpos.map((el, index) => (
                            <tr key={index}>
                              <td>
                                {/* <i className="fab fa-angular fa-lg text-danger me-3" /> */}
                                <Link
                                  to={""}
                                  onClick={() => {
                                    setshowfpodetails(true);
                                    setselectedfpo(el);
                                  }}
                                >
                                  <span className="fw-medium">{el.name}</span>
                                </Link>
                              </td>
                              <td>{el.members}</td>
                              <td>{el.promoter}</td>
                              <td>{el.sharePerMember}</td>
                              <td>{el.numberOfDirectors}</td>
                              <td>{el?.admin?.username}</td>
                              <td>
                                {" "}
                                {moment(el.registeredOn).format(
                                  " Do MMMM YYYY "
                                )}
                              </td>
                              <td>
                                <div className="d-flex align-items-center ">
                                  {" "}
                                  <span
                                    className={`badge  me-1 ${
                                      el.isBlocked
                                        ? "bg-label-danger"
                                        : "bg-label-primary"
                                    }`}
                                  >
                                    <i
                                      class={`fa-solid  fa-circle fa-beat-fade ${
                                        el.isBlocked
                                          ? "text-danger"
                                          : "text-success"
                                      }`}
                                    ></i>
                                    &nbsp;{el.isBlocked ? "Blocked" : "Active"}
                                  </span>
                                </div>
                                {/* <div className="dropdown">
                                <button
                                  type="button"
                                  className="btn p-0 dropdown-toggle hide-arrow"
                                  data-bs-toggle="dropdown"
                                >
                                  <i className="bx bx-dots-vertical-rounded" />
                                </button>
                                <div className="dropdown-menu">
                                  <a
                                    className="dropdown-item"
                                    href="javascript:void(0);"
                                  >
                                    <i className="bx bx-block me-1" /> Block
                                  </a>
                                  <a
                                    className="dropdown-item"
                                    href="javascript:void(0);"
                                  >
                                    <i className="bx bx-trash me-1" /> Delete
                                  </a>
                                </div>
                              </div> */}
                              </td>
                              <td>
                                <div className="d-flex ">
                                  <button
                                    type="button"
                                    data-bs-toggle="modal"
                                    data-bs-target="#modalCenter"
                                    className="btn btn-sm btn-outline-primary"
                                    onClick={() => openEditModal(el)}
                                  >
                                    <i className="bx bxs-pencil"></i>
                                  </button>
                                </div>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                    <div
                      className="modal fade"
                      id="modalCenter"
                      tabindex="-1"
                      aria-hidden="true"
                    >
                      <div
                        className="modal-dialog modal-dialog-centered"
                        role="document"
                      >
                        <div className="modal-content">
                          <div className="modal-header">
                            <h5 className="modal-title" id="modalCenterTitle">
                              Edit FPO details
                            </h5>
                            <button
                              type="button"
                              className="btn-close"
                              data-bs-dismiss="modal"
                              aria-label="Close"
                            ></button>
                          </div>
                          <div className="modal-body">
                            <form>
                              <div className="mb-3">
                                <label
                                  htmlFor="editName"
                                  className="form-label"
                                >
                                  Name
                                </label>
                                <input
                                  type="text"
                                  className="form-control"
                                  id="editName"
                                  name="name"
                                  value={editFormData.name}
                                  onChange={handleEditFormChange}
                                />
                              </div>
                              <div className="mb-3">
                                <label
                                  htmlFor="editRegisteredOn"
                                  className="form-label"
                                >
                                  Registered On
                                </label>
                                <input
                                  type="date"
                                  className="form-control"
                                  id="editRegisteredOn"
                                  name="registeredOn"
                                  value={editFormData.registeredOn}
                                  onChange={handleEditFormChange}
                                />
                              </div>
                              <div className="mb-3">
                                <label
                                  htmlFor="editPromoter"
                                  className="form-label"
                                >
                                  Promoter
                                </label>
                                <input
                                  type="text"
                                  className="form-control"
                                  id="editPromoter"
                                  name="promoter"
                                  value={editFormData.promoter}
                                  onChange={handleEditFormChange}
                                />
                              </div>
                              <div className="mb-3">
                                <label
                                  htmlFor="editSharePerMember"
                                  className="form-label"
                                >
                                  Share per Member
                                </label>
                                <input
                                  type="text"
                                  className="form-control"
                                  id="editSharePerMember"
                                  name="sharePerMember"
                                  value={editFormData.sharePerMember}
                                  onChange={handleEditFormChange}
                                />
                              </div>
                              <div className="mb-3">
                                <label
                                  htmlFor="editNumberOfDirectors"
                                  className="form-label"
                                >
                                  No. of Directors
                                </label>
                                <input
                                  type="text"
                                  className="form-control"
                                  id="editNumberOfDirectors"
                                  name="numberOfDirectors"
                                  value={editFormData.numberOfDirectors}
                                  onChange={handleEditFormChange}
                                />
                              </div>
                            </form>
                          </div>
                          <div className="modal-footer">
                            <button
                              type="button"
                              className="btn btn-outline-secondary"
                              data-bs-dismiss="modal"
                              onClick={() => setEditFpo(null)}
                            >
                              Close
                            </button>
                            <button
                              type="button"
                              className="btn btn-primary"
                              data-bs-dismiss="modal"
                              onClick={submitEditForm}
                            >
                              Save changes
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
              {showfpodetails && selectedfpo && (
                <div className="container-xxl flex-grow-1 container-p-y">
                  <h4 className="py-3 mb-4">
                    {selectedfpo && showfpodetails && (
                      <button
                        className="btn btn-primary me-2"
                        onClick={() => {
                          setshowfpodetails(false);
                          setselectedfpo(null);
                        }}
                      >
                        {" "}
                        <i className="fa-solid fa-angle-left"></i> &nbsp; FPO
                        list
                      </button>
                    )}
                    <span className="text-muted fw-light">FPO List /</span>{" "}
                    {selectedfpo.name}
                  </h4>
                  <div className="my-fpo-main">
                    <div className="my-fpodetails row">
                      <div className="fpo-img col-md-4">
                        <div className="p-3 card d-flex fpo-pic-main justify-content-center align-items-center flex-column">
                          <img
                            src={`${renderUrl}uploads/fpo/${selectedfpo?.fpopic}`}
                            alt="fpo"
                            className="fpopic shadow"
                          />
                          <h4 className="py-3 text-capitalize fpo-name-heading">
                            {selectedfpo?.name}
                          </h4>
                        </div>
                      </div>
                      <div className="col-md-8">
                        {" "}
                        <div className="row w-100 px-3">
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons">
                                <i className="fa-solid fa-user"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons">Admin</div>
                              <div className="col-12 fpo-info text-center">
                                {selectedfpo?.admin.username}
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons1">
                                <i className="fa-solid fa-users"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons">Members</div>
                              <div className="col-12 fpo-info text-center">
                                {selectedfpo?.members}
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons2 ">
                                <i className="fa-regular fa-calendar-days"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons">
                                Registered On
                              </div>
                              <div className="col-12 fpo-info text-center">
                                {moment(selectedfpo?.registeredOn).format(
                                  "Do MMMM YYYY"
                                )}
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons ">
                                <i className="fa-solid fa-arrow-up-right-dots"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons">Promoter</div>
                              <div className="col-12 fpo-info text-center">
                                {selectedfpo?.promoter}
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons1 ">
                                <i className="fa-solid fa-layer-group"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons">
                                Share per member
                              </div>
                              <div className="col-12 fpo-info text-center">
                                {selectedfpo?.sharePerMember}
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6 ">
                            <div className="row w-100  rounded shadow bg-white p-3 m-2">
                              <div className="col-2 fpo-icons2 ">
                                <i className="fa-solid fa-user-tie"></i>
                              </div>{" "}
                              <div className="col-10  fpo-icons ">
                                No. of Directors
                              </div>
                              <div className="col-12 fpo-info text-center">
                                {selectedfpo?.numberOfDirectors}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>{" "}
                </div>
              )}
            </div>
          </div>
        </div>

        {/* //   <!-- Overlay --> */}
        <div className="layout-overlay layout-menu-toggle"></div>
      </div>
      {/* // <!-- / Layout wrapper --> */}
    </div>
  );
};

export default Allfpo;
