import React, { useEffect, useState } from "react";
import Navigation from "../../components/Navigation";
import Topbar from "../../components/Topbar";
import { axiosInstance } from "../../config";
import Swal from "sweetalert2";
import jwtDecode from "jwt-decode";
import { Link, useNavigate, useParams } from "react-router-dom";
import Unauthorisedpage from "../../components/Unauthorisedpage";

const Editsurvey = () => {
  const token = localStorage.getItem("admin");
  const decoded = jwtDecode(token);
  const id = decoded.id;
  const { surveyId } = useParams();
  console.log(id);
  const [title, setTitle] = useState("");
  const [questions, setQuestions] = useState([{ question: "", options: [""] }]);
  const navigate = useNavigate("");

  useEffect(() => {
    const fetchSurvey = async () => {
      try {
        // Replace with the actual survey ID
        const response = await axiosInstance.get(
          `survey/getsurvey/${surveyId}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        if (response.status === 200) {
          const survey = response.data;
          setTitle(survey.title);
          setQuestions(survey.questions);
        }
      } catch (error) {
        // Handle any errors, e.g., show an error message
        console.error("Failed to fetch survey:", error);
      }
    };

    fetchSurvey();
  }, []);

  const addQuestion = () => {
    setQuestions([...questions, { question: "", options: [""] }]);
  };

  const addOption = (questionIndex) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].options.push("");
    setQuestions(updatedQuestions);
  };

  const handleQuestionChange = (questionIndex, newText) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].question = newText;
    setQuestions(updatedQuestions);
  };

  const handleOptionChange = (questionIndex, optionIndex, newText) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].options[optionIndex] = newText;
    setQuestions(updatedQuestions);
  };

  const removeQuestion = (questionIndex) => {
    if (questions.length === 1) {
      return;
    }

    const updatedQuestions = [...questions];
    updatedQuestions.splice(questionIndex, 1);
    setQuestions(updatedQuestions);
  };

  const removeOption = (questionIndex, optionIndex) => {
    if (questions[questionIndex].options.length === 1) {
      return;
    }

    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].options.splice(optionIndex, 1);
    setQuestions(updatedQuestions);
  };

  //   edit survey func
  const editSurvey = async () => {
    try {
      const response = await axiosInstance.post(
        `survey/edit/${surveyId}`,
        {
          title,
          questions,
          adminid: id,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (response.status === 200) {
        Swal.fire("Success", "Survey updated successfully", "success");
        navigate("/surveylist");
      }
    } catch (error) {
      // Display an error message
      Swal.fire("Error", "Failed to update the survey", "error");
    }
  };

  return (
    <div>
      {/* <!-- Layout wrapper --> */}
      <div className="layout-wrapper layout-content-navbar">
        <div className="layout-container">
          {/* <!-- Menu --> */}

          <Navigation />
          {/* <!-- / Menu --> */}

          {/* <!-- Layout container --> */}
          <div className="layout-page">
            <Topbar />
            {/* <!-- / Layout page --> */}
            <div className="content-wrapper">
              {/* <button onClick={editSurvey}>Create Survey</button> */}
              {decoded.role === "fpoadmin" &&
              decoded.permissions?.includes("editsurvey") ? (
                <div className="container-xxl flex-grow-1 container-p-y">
                  <div className="row">
                    <div className="col-xl">
                      <h3>Edit Survey</h3>

                      <div className="card  my-2">
                        <div className="card-header">
                          <div>
                            <label className="form-label" htmlFor={title}>
                              Survey Title
                            </label>
                          </div>
                          <div>
                            <input
                              type="text"
                              className="form-control"
                              id="surveytitle"
                              placeholder="Enter Title"
                              value={title}
                              onChange={(e) => setTitle(e.target.value)}
                            />
                          </div>
                        </div>
                      </div>
                      {questions.length === 0 && (
                        <div>
                          <button
                            className="btn btn-success my-2"
                            onClick={addQuestion}
                          >
                            <i className="fa-solid fa-plus"></i>&nbsp; Add
                            Question
                          </button>
                        </div>
                      )}
                      {questions.map((question, questionIndex) => (
                        <div className="card my-4">
                          <div className="card-body">
                            <div key={questionIndex} className="mb-3">
                              <div className="d-flex justify-content-between align-items-center">
                                <label
                                  className="form-label"
                                  htmlFor={`question-${questionIndex}`}
                                >
                                  Question {questionIndex + 1}
                                </label>
                                {questionIndex > 0 && (
                                  <button
                                    className="btn btn-danger m-2"
                                    onClick={() =>
                                      removeQuestion(questionIndex)
                                    }
                                  >
                                    <i className="fa-solid fa-trash"></i>&nbsp;
                                    Remove
                                  </button>
                                )}
                              </div>
                              <input
                                type="text"
                                className="form-control"
                                id={`question-${questionIndex}`}
                                placeholder="Enter your question"
                                value={question.question}
                                onChange={(e) =>
                                  handleQuestionChange(
                                    questionIndex,
                                    e.target.value
                                  )
                                }
                              />

                              <div className="row">
                                {question.options.map((option, optionIndex) => (
                                  <div
                                    key={optionIndex}
                                    className="my-2 col-md-6  col-12"
                                  >
                                    <div className="p-3 shadow rounded">
                                      <label
                                        className="form-label"
                                        htmlFor={`option-${questionIndex}-${optionIndex}`}
                                      >
                                        Option {optionIndex + 1}
                                      </label>
                                      <div className="input-group">
                                        <input
                                          type="text"
                                          className="form-control"
                                          id={`option-${questionIndex}-${optionIndex}`}
                                          aria-label="Recipient's username"
                                          aria-describedby="button-addon2"
                                          placeholder="Enter option"
                                          value={option}
                                          onChange={(e) =>
                                            handleOptionChange(
                                              questionIndex,
                                              optionIndex,
                                              e.target.value
                                            )
                                          }
                                        />{" "}
                                        {optionIndex > 0 && (
                                          <button
                                            className="btn btn-danger"
                                            id="button-addon2"
                                            onClick={() =>
                                              removeOption(
                                                questionIndex,
                                                optionIndex
                                              )
                                            }
                                          >
                                            <i className="fa-solid fa-trash"></i>
                                          </button>
                                        )}
                                      </div>

                                      {optionIndex ===
                                        question.options.length - 1 && (
                                        <button
                                          className="btn btn-primary my-2"
                                          onClick={() =>
                                            addOption(questionIndex)
                                          }
                                        >
                                          <i className="fa-solid fa-plus"></i>
                                          &nbsp; Add Option
                                        </button>
                                      )}
                                    </div>
                                  </div>
                                ))}
                              </div>
                            </div>

                            {questionIndex === questions.length - 1 && (
                              <div>
                                <button
                                  className="btn btn-success my-2"
                                  onClick={addQuestion}
                                >
                                  <i className="fa-solid fa-plus"></i>&nbsp; Add
                                  Question
                                </button>
                              </div>
                            )}
                          </div>
                        </div>
                      ))}
                      <div className="d-flex justify-content-around">
                        <Link
                          className="btn btn-xl btn-outline-secondary my-2"
                          to="/surveylist"
                        >
                          Cancel
                        </Link>
                        <button
                          className="btn btn-xl btn-primary my-2"
                          onClick={editSurvey}
                        >
                          <i className="fa-solid fa-floppy-disk"></i>&nbsp; Edit
                          Survey
                        </button>
                      </div>
                      <div className="text-center"></div>
                    </div>
                  </div>
                </div>
              ) : (
                <Unauthorisedpage />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Editsurvey;
