import React, { useEffect, useState } from "react";
import Navigation from "../../components/Navigation";
import Topbar from "../../components/Topbar";
import { axiosInstance, renderUrl } from "../../config";
import jwtDecode from "jwt-decode";
import moment from "moment";
import { Link, useParams } from "react-router-dom";
import Unauthorisedpage from "../../components/Unauthorisedpage";

const SeasonSingle = () => {
  const token = localStorage.getItem("admin");
  const decoded = jwtDecode(token);
  const id = decoded.id;
  //   console.log(decoded);
  const { seaid, seasonname } = useParams();

  const [figs, setfigs] = useState([]);
  const [error, setError] = useState(null);
  //   setshowfigdetails
  const [showfigdetails, setshowfigdetails] = useState(null);
  const fetchFIGs = async () => {
    try {
      let url = `radio/admin/all-radio-season/${seaid}`;

      //   if (decoded.role === "fpoadmin") {
      //     url = `survey/fposurvey/${id}`;
      //   }

      const response = await axiosInstance.get(url, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      console.log(response.data);
      setfigs(response.data.radio);
    } catch (err) {
      console.log(err);
      setError("Error while fetching admin list");
    }
  };

  useEffect(() => {
    fetchFIGs();
  }, [seaid]);

  const toggleSurveyStatus = async (id, activate) => {
    try {
      const response = await axiosInstance.patch(
        `radio/admin/update-radio-status/${id}/${activate}`,
        {},
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      // const updatedSurvey = response.data.updateCat;
      fetchFIGs();
    } catch (error) {
      console.error("Error:", error);
    }
  };
  return (
    <div>
      {/* <!-- Layout wrapper --> */}
      <div className="layout-wrapper layout-content-navbar">
        <div className="layout-container">
          {/* <!-- Menu --> */}
          <Navigation />
          {/* <!-- / Menu --> */}
          {/* <!-- Layout container --> */}
          <div className="layout-page">
            <Topbar />
            {/* <!-- / Layout page --> */}
            <div className="content-wrapper">
              {decoded.role === "superadmin" ||
              decoded.permissions?.includes("radiop") ? (
                <>
                  {figs && (
                    <div className="container-xxl flex-grow-1 container-p-y">
                      <h4 className="py-3 mb-4">
                        <span className="text-muted fw-light">Seasons /</span>
                        {seasonname || "Season"}
                      </h4>
                      {/* Basic Bootstrap Table */}
                      <div className="card">
                        <h5 className="card-header">Radio / Episodes</h5>
                        <div className="table-responsive text-nowrap">
                          <table className="table">
                            <thead>
                              <tr>
                                <th>
                                  <b>S. No.</b>
                                </th>
                                <th>
                                  <b>Episode/Radio name</b>
                                </th>
                                <th>
                                  <b>Image</b>
                                </th>
                                <th>
                                  <b>Recording</b>
                                </th>
                                <th>
                                  <b>Posted by</b>
                                </th>
                                <th>
                                  <b>Status</b>
                                </th>
                                {/* <th>
                                  <b>Status</b>
                                </th> */}
                                {(decoded.permissions?.includes(
                                  "editradiodetails"
                                ) ||
                                  decoded.role === "superadmin") && (
                                  <th>
                                    <b>Action</b>
                                  </th>
                                )}
                                {/* {decoded.role === "fpoadmin" && (
                                <th>
                                  <b>Actions</b>
                                </th>
                              )} */}
                              </tr>
                            </thead>
                            <tbody className="table-border-bottom-0">
                              {figs.map((el, index) => (
                                <tr key={index}>
                                  <td>{index + 1}</td>
                                  <td>
                                    {/* <i className="fab fa-angular fa-lg text-danger me-3" /> */}
                                    <Link
                                      to={`/radio/${el._id}`}
                                      //   onClick={() => setshowfigdetails(el)}
                                    >
                                      <span className="fw-medium">
                                        {el.heading}
                                      </span>
                                    </Link>
                                  </td>
                                  <td>
                                    <img
                                      src={`${renderUrl}uploads/radio/${el.image}`}
                                      // width={100}
                                      className="intoggle-table-image"
                                      alt="cat-img"
                                    />
                                  </td>
                                  <td>{el.recording}</td>
                                  {/* <td>
                                <span className="badge bg-label-primary me-1">
                                    Active
                                  </span>
  
                                {el?.memberId?.name}
                              </td> */}

                                  <td>{el.host}</td>
                                  <td>
                                    <div className="d-flex align-items-center">
                                      {" "}
                                      <span
                                        className={`badge  me-1 ${
                                          el.status === 1
                                            ? "bg-label-primary"
                                            : el.status === 2
                                            ? "bg-label-danger"
                                            : "bg-label-warning"
                                        }`}
                                      >
                                        <i
                                          class={`fa-solid  fa-circle fa-beat-fade ${
                                            el.status === 1
                                              ? "text-success"
                                              : el.status === 2
                                              ? "text-danger"
                                              : "text-warning"
                                          }`}
                                        ></i>
                                        &nbsp;
                                        {el.status === 1
                                          ? "active"
                                          : el.status === 2
                                          ? "blocked"
                                          : "inactive"}
                                      </span>
                                    </div>
                                  </td>
                                  {(decoded.permissions?.includes(
                                    "editradiodetails"
                                  ) ||
                                    decoded.role === "superadmin") && (
                                    <td>
                                      <div className="dropdown">
                                        <button
                                          type="button"
                                          className="btn btn-primary  dropdown-toggle hide-arrow"
                                          data-bs-toggle="dropdown"
                                        >
                                          <i className="fa-solid fa-pen-to-square"></i>
                                        </button>
                                        <div className="dropdown-menu">
                                          {el.status === 1 ? (
                                            <>
                                              <a
                                                className="dropdown-item"
                                                href="javascript:void(0);"
                                                onClick={() =>
                                                  toggleSurveyStatus(el._id, 0)
                                                }
                                              >
                                                <i className="fa-solid fa-circle text-warning"></i>{" "}
                                                Deactivate
                                              </a>
                                              <a
                                                className="dropdown-item"
                                                href="javascript:void(0);"
                                                onClick={() =>
                                                  toggleSurveyStatus(el._id, 2)
                                                }
                                              >
                                                <i className="fa-solid fa-circle text-danger"></i>{" "}
                                                Block
                                              </a>
                                            </>
                                          ) : el.status === 0 ? (
                                            <>
                                              <a
                                                className="dropdown-item"
                                                href="javascript:void(0);"
                                                onClick={() =>
                                                  toggleSurveyStatus(el._id, 1)
                                                }
                                              >
                                                <i className="fa-solid fa-circle text-success"></i>{" "}
                                                Activate
                                              </a>
                                              <a
                                                className="dropdown-item"
                                                href="javascript:void(0);"
                                                onClick={() =>
                                                  toggleSurveyStatus(el._id, 2)
                                                }
                                              >
                                                <i className="fa-solid fa-circle text-danger"></i>{" "}
                                                Block
                                              </a>
                                            </>
                                          ) : (
                                            <a
                                              className="dropdown-item"
                                              href="javascript:void(0);"
                                              onClick={() =>
                                                toggleSurveyStatus(el._id, 0)
                                              }
                                            >
                                              <i className="fa-solid fa-circle text-success"></i>{" "}
                                              Unblock
                                            </a>
                                          )}
                                        </div>
                                      </div>
                                    </td>
                                  )}
                                  {/* <td>
                                  {" "}
                                  {moment(el.createdAt).format(" Do MMMM YYYY ")}
                                </td> */}
                                  {/* {decoded.role === "fpoadmin" && (
                                  <td>
                                    <div>
                                      <Link
                                        className="btn btn-sm btn-outline-primary"
                                        to={`/editsurvey/${el._id}`}
                                      >
                                        <i className="bx bx-edit-alt me-1" /> Edit
                                      </Link>
                                    </div>
                                  </td>
                                )} */}
                                </tr>
                              ))}
                            </tbody>
                          </table>
                          {figs.length === 0 && (
                            <div>
                              <p className="text-center my-3">No figs found</p>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  )}
                </>
              ) : (
                <Unauthorisedpage />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SeasonSingle;
